@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;500;900&display=swap);

* {
  box-sizing: border-box;
  background-color: #f9faf3;
  /* border: black 1px solid; */
}

body {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  color: #4a6163;
}

h1, h2 {
  font-family: 'Rubik', sans-serif;
}

.header__h1 {
  margin: auto 0;
  font-weight: 800;
  font-size: 5rem;
  color: #f17a7e !important;
  margin-bottom: 15px;
  text-align: left;
}

.header__text--stroke {
  -webkit-text-stroke: 2px #f9a66c;
  color: rgba(0, 0, 0, 0);
}

textarea, input, button, select {
  font-family: inherit; 
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
  border-radius: 5px;
  width: 98%;
  margin-top: 10px;
  margin-bottom: 10px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .header__h1 {
    text-align: center;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  * {
    box-sizing: content-box;
  }

  .header__h1 {
    font-size: 6rem;
    margin: 5% 10% 5% 0;
    max-width: 300px;
  }

  textarea, input, button, select {
    width: 90%;
  }
}
.board-container {
  margin-left: 5%;
}

.board-container__nav {
  margin-top: 2%;
  top: 0;
  position: fixed;
  display: flex;
  font-size: smaller;
  align-items: center;
  z-index: 1;
}

.board-container__nav span {
  white-space: nowrap;
  margin-right: 15px;
}

.board-container__left-side {
  position: fixed;
  width: 25%;
  margin-right: 10% !important;
}

.board-container__content {
  margin-top: 5%;
}

.board-container__content__cards {
  margin-left: 30%;
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.alert-display {
  font-weight: 500;
  margin-bottom: 1.5em;
}

.alert-display span {
  background-color: #ffc94b;
}

@media (min-width: 481px) and (max-width: 767px) {
  .board-container {
    margin-left: auto;
  }

  .board-container__nav {
    position: static;
    width: 40%;
    margin-left: 7%;
  }

  .board-container__left-side {
    width: 100%;
    position: static;
    display: flex;
    margin-left: 7%;
  }

  .board-container__content__cards {
    margin: auto;
    width: 100%;
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  .board-container {
    margin: auto;
  }
  
  .board-container__nav {
    position: static;
    z-index: 0;
    width: 80%;
  }

  .board-container__left-side {
    position: static;
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .board-container__content__cards {
    width: 100%;
    margin: auto;
  }
}
.bg-color-yellow {
  background-color: #FFE7AD;
}

.bg-color-pink {
  background-color: #F9B9B4;
}

.bg-color-orange {
  background-color: #FEC886;
}

.card {
  padding: 0.5rem 0;
  margin: 0.5rem;

  min-height: 250px;
  min-width: 225px;
  flex-basis: 18%;

  border-radius: 5px;

  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  grid-template-rows: 0.5fr 3fr 0.5fr;
  align-items: center;
  overflow: hidden;
}

.card__content {
  grid-column-start: 2;
  grid-row-start: 2;

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  text-align: center;
  font-weight: 100;
  overflow: hidden;
}

.card__content-text {
  text-overflow: ellipsis;
  overflow: hidden;
}

.card__content-emoji {
  font-size: 3rem;
}

.card__content-text, .card__content-emoji {
  padding: 0;
  margin: 0;
}

.delete-button {
  font-family: 'Permanent Marker', Helvetica, sans-serif;
  margin-left: 30%;
  border-radius: 25px;
  font-size: smaller;
}

@media (min-width: 481px) and (max-width: 767px) {
  .card {
    min-height: 225px;
    min-width: 200px;
  } 

  .delete-button {
    font-size: 0.7em;
  }
}
.new-card-form__bg-color {
  background-color: #FFE099;
}

.new-card-form {
  padding: 1em 0;
  margin: 0.5rem;
  height: 250px;
  width: 225px;
  border-radius: 5px;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  align-items: center;
  box-shadow: 10px 10px 0px 0px #E29578;
}

.new-card-form__form-button {
  background-color: #f5847b;
  border: 1px solid #4a6163;
  font-size: 1em;
  border-radius: 5px;
}

.new-card-form__form-button:hover {
  background-color: #7cb518;
  font-style: italic;
}

